// eslint-disable-next-line @typescript-eslint/triple-slash-reference
/// <reference path="../dist/gui.d.ts" />

export default class TestScrollBar {

    public constructor(app: vf.Application, uiStage: vf.gui.Stage) {
        this.onLoad(app, uiStage)
    }
    private _title?: vf.gui.Label;
    private _testScrollBar?: vf.gui.ScrollBar;
    private _buttonList: vf.gui.Label[] = [];

    private onLoad(app: vf.Application, uiStage: vf.gui.Stage) {

        const title = this._title = new vf.gui.Label("scrooll组件，配合ScrollingContainer使用");
        title.x = 450;
        title.y = 250;
        uiStage.addChild(title);

        const sc = this.getScrollingContainer(uiStage, 500, 300, "拖拽进度条或拖拽图片");

        /** 滑动条容器 vertical = false */
        const vscrollBar = this._testScrollBar = new vf.gui.ScrollBar();//参数2设置sourceTrack的9宫拉伸
        vscrollBar.thumb = `data:image/png;base64,`;
        vscrollBar.track = "data:image/png;base64,";
        vscrollBar.x = 0;
        vscrollBar.style.right = 0;
        vscrollBar.width = 20;
        vscrollBar.height = sc.height;
        vscrollBar.source = sc;//绑定vf.gui.ScrollingContainer
        vscrollBar.vertical = true;//位置必须在实例后
        vscrollBar.autohide = true; //当内容不需要滚动时，隐藏
        vscrollBar.value = 0;
        sc.addChild(vscrollBar);


        // /** 滑动条容器 vertical = true */
        // let hscrollBar = new vf.gui.ScrollBar(0, 1);
        // hscrollBar.sourceThumb = "assets/skin/ScrollBar/roundthumb.png";
        // hscrollBar.sourceTrack = "assets/skin/ScrollBar/track_sb.png";
        // hscrollBar.x = sc.x;
        // hscrollBar.y = sc.y + sc.height + 20;
        // hscrollBar.width = sc.width;
        // hscrollBar.height = 10;
        // hscrollBar.scrollingContainer = sc;//绑定vf.gui.ScrollingContainer
        // hscrollBar.value = 0;
        // hscrollBar.vertical = false;
        // hscrollBar.autohide = true;
        // uiStage.addChild(hscrollBar);

        this.getButton('设置/取消thumb', () => {
            if (vscrollBar.thumb !== "") {
                vscrollBar.thumb = "";

            } else {
                vscrollBar.thumb = `data:image/png;base64,`;

            }
        }, uiStage, 15, 0);


        this.getButton('设置/取消track', () => {
            if (vscrollBar.track !== "") {
                vscrollBar.track = "";

            } else {
                vscrollBar.track = "data:image/png;base64,";

            }
        }, uiStage, 15, 0);

        this.getButton('设置/取消width', () => {
            if (vscrollBar.width !== 100) {
                vscrollBar.width = 100;

            } else {
                vscrollBar.width = 20;

            }
        }, uiStage, 15, 0);


        this.getButton('设置/取消height', () => {
            if (vscrollBar.height) {
                vscrollBar.height = 100;

            } else {
                vscrollBar.height = sc.height;

            }
        }, uiStage, 15, 0);


        this.getButton('设置/取消vertical', () => {
            if (vscrollBar.vertical !== false) {
                vscrollBar.vertical = false;

            } else {
                vscrollBar.vertical = true;

            }
        }, uiStage, 15, 0);


        this.getButton('设置/取消source', () => {
            if (vscrollBar.source) {
                vscrollBar.source = undefined;

            } else {
                vscrollBar.source = this.getScrollingContainer(uiStage, 500, 300, "拖拽进度条或拖拽图片");

            }
        }, uiStage, 15, 0);




    }


    private getScrollingContainer(uiStage: vf.gui.Stage, x: number, y: number, label: string) {
        const sc = new vf.gui.ScrollingContainer();
        sc.style.backgroundColor = 0xffffff;
        sc.x = x;
        sc.y = y;
        sc.width = 500;
        sc.height = 300;

        const img = new vf.gui.Image();
        img.src = "assets/sprite.png";
        img.y = 0;
        sc.addChild(img);

        const img2 = new vf.gui.Image();
        img2.src = "assets/dino.png";
        img2.x = 10;
        img2.y = 700;
        sc.addChild(img2);

        const img3 = new vf.gui.Image();
        img3.src = "assets/dino.png";
        img3.x = 300;
        img3.y = 780;
        sc.addChild(img3);

        const t = new vf.gui.Label(label);
        t.style.color = 0x000000;
        t.y = 0;
        sc.addChild(t);

        uiStage.addChild(sc);
        return sc;
    }

    private posY = 10;
    private getButton(title: string, fun: Function, target: any, x: number, y: number) {
        const button = new vf.gui.Label;
        button.style.fontSize = 24;
        button.style.fontFamily = 'PingFang-SC-Medium,PingFang-S';
        button.style.fontWeight = 500;
        button.style.color = 0xffffff;
        button.style.lineHeight = 45;
        button.text = title;
        button.interactabled = true;
        button.x = x;
        button.y = this.posY;
        button.on(vf.gui.Interaction.TouchMouseEvent.onClick, fun);
        target.addChild(button);
        this.posY = this.posY + 25 + y;
        this._buttonList.push(button);
        return button;
    }


    private onRelease() {
        if (this._testScrollBar) {
            this._testScrollBar.removeAllListeners();
            this._testScrollBar.parent && this._testScrollBar.parent.removeChild(this._testScrollBar);
            this._testScrollBar.release();
            this._testScrollBar = undefined;
        }
        if (this._title) {
            this._title.removeAllListeners();
            this._title.parent && this._title.parent.removeChild(this._title);
            this._title.release();
            this._title = undefined;
        }
        if (this._buttonList && this._buttonList.length > 0) {
            this._buttonList.forEach(element => {
                element.removeAllListeners();
                element.parent && element.parent.removeChild(element);
                element.release();
            });
            this._buttonList = [];
        }
    }



}


